:root, [data-theme="default"] {
    // main
    @include defineColorHSL(--color-primary-darker, 197, 64%, 38%);
    @include defineColorHSL(--color-primary-dark, 197, 64%, 48%);
    @include defineColorHSL(--color-primary, 197, 64%, 58%);
    @include defineColorHSL(--color-primary-light, 197, 64%, 68%);
    @include defineColorHSL(--color-primary-lighter, 197, 64%, 78%);

    @include defineColorHSL(--color-accent-darker, 0, 0%, 18%);
    @include defineColorHSL(--color-accent-dark, 0, 0%, 28%);
    @include defineColorHSL(--color-accent, 0, 0%, 38%);
    @include defineColorHSL(--color-accent-light, 0, 0%, 48%);
    @include defineColorHSL(--color-accent-lighter, 0, 0%, 58%);

    @include defineColorHSL(--color-black, 240, 8%, 12%);
    @include defineColorHSL(--color-white, 0, 0%, 100%);

    // feedback
    @include defineColorHSL(--color-warning-darker, 46, 100%, 22%);
    @include defineColorHSL(--color-warning-dark, 46, 100%, 32%);
    @include defineColorHSL(--color-warning, 46, 100%, 42%);
    @include defineColorHSL(--color-warning-light, 46, 100%, 52%);
    @include defineColorHSL(--color-warning-lighter, 46, 100%, 62%);

    @include defineColorHSL(--color-success-darker, 135, 47%, 41%);
    @include defineColorHSL(--color-success-dark, 135, 47%, 51%);
    @include defineColorHSL(--color-success, 135, 47%, 61%);
    @include defineColorHSL(--color-success-light, 135, 47%, 71%);
    @include defineColorHSL(--color-success-lighter, 135, 47%, 81%);

    @include defineColorHSL(--color-error-darker, 350, 75%, 23%);
    @include defineColorHSL(--color-error-dark, 350, 75%, 33%);
    @include defineColorHSL(--color-error, 350, 75%, 43%);
    @include defineColorHSL(--color-error-light, 350, 75%, 53%);
    @include defineColorHSL(--color-error-lighter, 350, 75%, 63%);

    @include defineColorHSL(--color-info-darker, 223, 36%, 38%);
    @include defineColorHSL(--color-info-dark, 223, 36%, 48%);
    @include defineColorHSL(--color-info, 223, 36%, 58%);
    @include defineColorHSL(--color-info-light, 223, 36%, 68%);
    @include defineColorHSL(--color-info-lighter, 223, 36%, 78%);

    // color contrasts
    @include defineColorHSL(--color-bg, 228, 10%, 10%);
    @include defineColorHSL(--color-contrast-lower, 228, 7%, 14%);
    @include defineColorHSL(--color-contrast-low, 228, 4%, 23%);
    @include defineColorHSL(--color-contrast-medium, 225, 2%, 55%);
    @include defineColorHSL(--color-contrast-high, 210, 3%, 87%);
    @include defineColorHSL(--color-contrast-higher, 210, 20%, 98%);
}

