header {

    &.header {
        background-color: var(--color-contrast-lower);
        position: relative;
        top: 0;
        bottom: auto;
        z-index: 10;

        .navbar-toggler {
            margin-right: 3rem;

            .navbar-toggler-icon {
                color: var(--color-contrast-high);
            }
        }

        .navbar {
            background-color: transparent;
            height: 85px;
            padding: 0;
        }

        .navbar-collapse {
            background-color: var(--color-contrast-lower);
        }

        a {
            font-family: $font-family-merienda;
            font-size: 18px;
            text-transform: uppercase;
            font-weight: $font-weight-bold;
        }


        .navbar-brand {
            padding: 0 0 0 3rem;
            margin: 0;

            &,
            img {
                max-width: 60px;
            }
        }

        .dropdown-toggle {
            &:after {
                border-top: 0;
            }
        }

        .nav-item {
            &.active {
                .nav-link {
                    font-weight: bold;
                    font-size: 20px;

                    &:after {
                        transform: scaleX(1);
                        transform-origin: left;
                    }
                }
            }
        }

        .nav-link {
            position: relative;
            min-height: 50px;

            &:after {
                content: "";
                position: absolute;
                bottom: 6px;
                left: 0;
                display: block;
                width: 100%;
                z-index: -1;
                height: 2px;
                border-radius: 10px;
                transform: scaleX(0);
                transition: .6s transform;
                transform-origin: 100% 0;
                background-color: var(--color-white);
            }

            &:hover {
                &:after {
                    transform: scaleX(1);
                    transform-origin: left;
                }
            }
        }

        @include media-breakpoint-up(lg) {
            background-color: transparent;
            top: 30px;
            .navbar {
                height: 80px;
            }
            .navbar-brand {
                padding: 0;

                &,
                img {
                    max-width: 150px;
                }

            }

            .navbar-collapse {
                background-color: transparent;
            }
            .navbar--login {
                position: absolute;
                right: 2rem;
            }
        }
    }
}
