body {
    background-image: linear-gradient(135deg, transparent 0%, transparent 21%, rgb(21, 22, 24) 21%, rgb(21, 22, 24) 48%, transparent 48%, transparent 95%, rgb(17, 17, 20) 95%, rgb(17, 17, 20) 100%), linear-gradient(45deg, transparent 0%, transparent 6%, rgb(16, 17, 20) 6%, rgb(16, 17, 20) 13%, rgb(38, 39, 45) 13%, rgb(38, 39, 45) 32%, transparent 32%, transparent 100%), linear-gradient(90deg, rgb(23, 24, 28), rgb(23, 24, 28));
    background-repeat: no-repeat;
}

.azuriom_is_game {
    height: 100vh;

    #app {
        &:before {
            display: none;
        }
    }

    #shops,
    #auth {
        margin: 3rem auto;
    }
}

#app {
    position: relative;

    @include media-breakpoint-up(lg) {
        &:before {
            content: "";
            position: absolute;
            top: 0;
            background: url("#{$image-path}92715618.png");
            background-position: bottom;
            width: 100%;
            height: 260px;

        }
    }
}

.content {
    margin-top: 11rem;
    margin-bottom: 11rem;
}

#preloader {
    z-index: 9999999;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-bg);
    flex-direction: column;

    .spinner-border {
        margin-top: 4rem;
        height: 60px;
        width: 60px;
        border-color: var(--color-contrast-high);
        border-right-color: transparent;
    }
}
