.home--glide {
    padding-top: 6rem;
    overflow-y: hidden;

    img {
        width: 100%;
        object-fit: cover;
        object-position: center;
        max-height: 500px;
    }

    .glide__slides {
        align-items: center;
    }

    .glide__slide {
        position: relative;
        zoom: 0;

        &:before,
        &:after {
            display: none;
        }
    }

    .glide__slide--clone,
    .glide__slide {
        img {
            opacity: 0.5;
            filter: blur(20px);
            transition: opacity 100ms ease-in-out, filter 200ms ease-in-out;
        }

        .card {
            .card-img-overlay {
                opacity: 0;
                transform: rotate(0deg) translateY(150%);
                transition: transform 200ms ease-in-out, opacity 200ms ease-in-out;

                &:before,
                &:after {
                    transform: rotate(0deg) translateY(100%);
                    transition: transform 400ms ease-in-out;
                }
            }
        }

    }

    .glide__slide--active {
        img {
            opacity: 1;
            filter: blur(0);
            transition: opacity 600ms ease-in-out, filter 200ms ease-in-out;
        }

        .card {
            .card-img-overlay {
                opacity: 1;
                transform: translateY(0);
                transition: transform 600ms ease-in-out, opacity 300ms ease-in-out;

                &:before,
                &:after {
                    transform: rotate(5deg) translateY(0);
                    transition: transform 300ms ease-in-out;
                }
            }
        }
    }

    .glide__arrows {
        display: none;

        button {
            background-color: transparent;
            border: 0;
            color: var(--color-contrast-medium);
            font-size: 1.5rem;
            margin: 0 1rem;
        }
    }

    .card {
        border: 0;
        position: relative;
        overflow: hidden;

        .card-img-overlay {
            position: absolute;
            top: auto;
            bottom: 10px;
            right: 0;
            left: 0;
            width: 100%;
            background-color: var(--color-bg);
            z-index: 1;

            &:before,
            &:after {
                display: none;
            }

            .card-title {
                color: var(--color-white);
                text-transform: uppercase;
            }

            .card-text {
                color: var(--color-contrast-medium);
            }
        }
    }

    @include media-breakpoint-up(md) {
        .glide__arrows {
            display: block;
            position: absolute;
            top: -20px;
            bottom: auto;
            right: 20px;
        }
        .card {
            .card-img-overlay {
                max-width: 400px;
                position: absolute;
                top: 60%;
                bottom: 0;
                right: 0;
                left: auto;
                width: 40vh;
                background-color: var(--color-bg);
                z-index: 1;

                &:before,
                &:after {
                    display: block;
                    position: absolute;
                    content: '';
                    top: -50px;
                    bottom: 0;
                    left: -40px;
                    right: auto;
                    background-color: var(--color-bg);
                    transform: rotate(5deg);

                    box-shadow: var(--shadow-lg);
                    z-index: -1;
                    height: 150%;
                    width: 100%;
                }

                &:after {
                    top: -60px;
                    left: auto;
                    right: -40px;
                    width: 110%;
                }
            }
        }

        .glide__slide--active {
            .card {
                .card-img-overlay {
                    opacity: 1;
                    transform: translateY(0) translateX(-45%);
                    transition: transform 600ms ease-in-out, opacity 300ms ease-in-out;
                }
            }
        }
    }
    @include media-breakpoint-up(xl) {
        .glide__slide {

            &:before,
            &:after {
                display: block;
                position: absolute;
                content: '';
                top: -50px;
                bottom: 0;
                left: -30px;
                right: auto;
                background-color: var(--color-bg);
                transform: rotate(5deg);
                box-shadow: var(--shadow-lg);
                z-index: 1;
                height: 150%;
                width: 110%;
                max-width: 100px;
            }

            &:after {
                top: -60px;
                left: auto;
                right: -30px;
                width: 110%;
            }
        }
    }
}

.home--page {
    padding-top: 8rem;
    @include card-bootstrap();

    nav {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.home--box {
    &-title {
        color: var(--color-white);
        font-size: $font-size-lg;
        text-transform: uppercase;
    }

    &-text {
        margin: 0.5rem 0;
    }
}
