// --------------------------------

// (START) Global editor code https://codyhouse.co/ds/globals/buttons

// --------------------------------


.btn { // style affecting all buttons
    --btn-padding-y: var(--space-xxs); // padding top/bottom
    --btn-padding-x: var(--space-sm); // padding left/right
    --btn-radius: 0.25em; // border radius
    --btn-font-size: 1em; // font size
    line-height: 1.2;
    box-shadow: var(--shadow-xs);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: var(--color-white);
    border-radius: 10px;
    display: block;
    border: 0 !important;

    &:hover {
        cursor: pointer;
        box-shadow: var(--shadow-sm);
        background-position: right center; /* change the direction of the change here */
        color: #fff;
        text-decoration: none;
    }

    &:focus {
        outline: none;
        box-shadow: 0px 0px 0px 2px alpha(var(--color-contrast-higher), 0.15);
    }

    &:active {
        transform: translateY(2px);
    }
}

// themes
.btn-primary,
.btn--primary {
    background-color: var(--color-primary);
    color: var(--color-white);

    &.btn-grad {
        background-image: linear-gradient(to right, #4776E6 0%, #8E54E9 51%, #4776E6 100%)
    }

    &:hover {
        background-color: var(--color-primary-dark);
    }

    &:focus {
        box-shadow: 0px 0px 0px 2px alpha(var(--color-primary), 0.2);
    }
}

.btn-subtle,
.btn--subtle {
    background-color: var(--color-contrast-lower);
    color: var(--color-contrast-higher);

}

.btn-accent,
.btn--accent {
    background-color: var(--color-accent);
    color: var(--color-white);

    &:focus {
        box-shadow: 0px 0px 0px 2px alpha(var(--color-accent), 0.2);
    }

    &.btn-grad {
        background-image: linear-gradient(to right, #00d2ff 0%, #3a7bd5 51%, #00d2ff 100%)
    }

}

.btn-register {
    background-image: linear-gradient(to right, #fd746c 0%, #ff9068 51%, #fd746c 100%)
}

.btn-support {
    background-image: linear-gradient(to right, #4CB8C4 0%, #3CD3AD 51%, #4CB8C4 100%)
}


.btn-love,
.btn--love {
    background-color: var(--color-primary);
    color: var(--color-white);

    &:focus {
        box-shadow: 0px 0px 0px 2px alpha(var(--color-primary), 0.2);
    }

    &.btn-grad {
        background-image: linear-gradient(to right, #00d2ff 0%, #3a7bd5 51%, #00d2ff 100%)
    }

}

// feedback
.btn--disabled,
.btn[disabled],
.btn[readonly] {
    opacity: 0.6;
    cursor: not-allowed;
}

// sizes
.btn--sm {
    font-size: 0.8em;
}

.btn--md {
    font-size: 1.2em;
}

.btn--lg {
    font-size: 1.4em;
}

// --------------------------------

// (END) Global editor code

