#shops {
    .far,
    .fab,
    .fas {
        font-size: 26px;
        padding-right: 1rem;
        vertical-align: sub;
    }

    .fa-coins {
        color: #e2c860;
    }

    .shops {

        &--account,
        &--cash {
            font-size: 18px;

            i {
            }

            a {
                display: inline-block;
                vertical-align: middle;
                color: var(--color-contrast-higher);
            }
        }

        &--account {
            a {
                display: block;
                text-align: center;

                i {
                    color: var(--color-contrast-higher);
                }
            }
        }

        &--list-group {
            box-shadow: var(--shadow-md);

            .list-group-item {
                background-color: var(--color-contrast-lower);
                padding: 0;
                transition: background-color 200ms linear;

                &:focus,
                &:hover,
                &.active {
                    background-color: var(--color-contrast-low);

                    a {
                        color: var(--color-contrast-higher);
                    }
                }

                a {
                    display: inline-block;
                    width: 100%;
                    padding: 0.75rem 1.25rem;
                    color: var(--color-contrast-high);
                    transition: color 200ms linear;
                }
            }

        }

        &--items {
            > .row {
                align-items: stretch;
            }

            form {
                height: 100%;
                display: flex;
            }

            .card {
                background-color: var(--color-contrast-low);
                border: 0;
                box-shadow: var(--shadow-md);
                display: block;

                h5 {
                    background-color: var(--color-contrast-lower);
                }

                .card-body {
                    color: var(--color-contrast-high);
                    text-align: center;

                    .shop--item-quantity {
                        position: relative;
                        text-align: left;
                        color: var(--color-contrast-high);
                        font-weight: $font-weight-bolder;

                        .form-control {
                            width: calc(100% - 50px);
                            height: 50px;
                            border: 0;
                            padding: 0 1rem;
                            border-top-right-radius: 0;
                            border-bottom-right-radius: 0;
                            font-weight: $font-weight-bolder;
                            color: var(--color-contrast-higher);
                        }

                        .quantity-nav {
                            position: absolute;
                            top: auto;
                            bottom: 0;
                            right: 0;
                            left: auto;
                            display: flex;
                            flex-direction: column;

                            .quantity-button {
                                text-align: center;
                                width: 50px;
                                height: 25px;
                                background-color: var(--color-contrast-lower);
                                color: var(--color-contrast-higher);
                                font-weight: $font-weight-bolder;
                                line-height: 25px;
                                cursor: pointer;

                                &:first-child {
                                    border-bottom: 1px solid var(--color-contrast-medium);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

#plugin--shop {
    @include card-bootstrap;

    .list-group {
        .list-group-item {
            background-color: var(--color-contrast-lower);
            padding: 0;
            transition: background-color 200ms linear;
            margin: 1rem 0;

            &:focus,
            &:hover,
            &.active {
                background-color: var(--color-contrast-low);

                a {
                    color: var(--color-contrast-higher);
                }
            }

            a {
                display: inline-block;
                width: 100%;
                padding: 0;
                color: var(--color-contrast-high);
                transition: color 200ms linear;

                &,
                h3,
                h4 {
                    font-family: $font-family-base;
                    margin-bottom: 0;
                }
            }
        }

        svg {
            width: 100%;
            max-width: 70px;
            float: left;
        }

        .card-body {
            padding: 0;
        }

        .logo,
        .name,
        .price {
            padding: 1rem;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .price {
            background-color: var(--color-contrast-low);
            float: right;
            width: 150px;
        }
    }

    .payment-method {
        background-color: var(--color-contrast-lower);
    }
}
